@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.main {
  margin-top: 5rem;
}
.main .main__contenedor {
  display: grid;
  grid-auto-columns: auto;
}
@media (min-width: 768px) {
  .main .main__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .main__imagen {
    grid-column: 2/3;
  }
}

.main__contenido {
  grid-row: 2/3;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .main__contenido {
    grid-row: 1/2;
    margin-top: 5rem;
  }
}
.main__contenido .main__parrafo {
  margin: 0;
  font-size: 3rem;
  font-weight: 900;
  width: 45rem;
}

.main__lista .elemento {
  color: gray;
}

.elemento--resaltado {
  font-size: 1.5rem;
  color: #333333;
}